.inicio{
    background-image: url(/src/img/header.jpg);
}
.header{
    background-color: $grisOscuro;
    padding: 1rem 0 1rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include tablet{
            min-height: 50rem;
        }
        .contenido-header{
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h4{
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        max-width: 50rem;
    }
}